const cumulativeRevenueChart = async (data?: any) => {
    const series: any = [];

    if (data) {
        // 객체의 키를 반복
        const promises = [];
        let index = 1;
        for (const key in data) {
            if (data.hasOwnProperty(key)) {
                const value = data[key];
                const amountNumbers = value.map((amountString: any) => {
                    // ,를 제거하고 숫자로 변환
                    const numberValue = parseInt(amountString.replace(/,/g, ''), 10);
                    return numberValue;
                });

                // 비동기 작업을 수행하는 Promise를 생성 및 배열에 추가
                const item = {
                    name: key,
                    data: amountNumbers,
                };
                series.push(item);
            }
            const results = await Promise.all(series);
        }
    }

    //     if (data) {
    //     // 비동기적으로 순회하려면 for...of 대신 forEach나 for...in을 사용할 수 있습니다.
    //     // 여기서는 forEach를 사용합니다.
    //     await Promise.all(Array.from(data).map(async ([key, value]) => {
    //         categories.push(key);

    //         // 여기에서도 어떤 비동기 작업을 수행한다면 await를 사용하세요.
    //         const processedValue = await someAsyncFunction(value);

    //         const item = {
    //             name: key,
    //             data: processedValue,
    //         };

    //         series.push(item);
    //     }));
    // }
    const returnData = {
        options: {
            chart: {
                height: 180,
                type: 'line',
                zoom: {
                    enabled: false,
                },
                stacked: true,
                toolbar: {
                    show: false,
                },
                dropShadow: {
                    enabled: true,
                    color: '#000',
                    top: 18,
                    left: 7,
                    blur: 10,
                    opacity: 0.2,
                },
            },

            dataLabels: {
                enabled: false,
            },
            // stroke: {
            //     curve: "straight",
            // },
            grid: {
                row: {
                    colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
                    opacity: 0.5,
                },
            },
            xaxis: {
                categories: ['1분기', '2분기', '3분기', '4분기'],
            },
            yaxis: {
                tickAmount: 6,
                forceNiceScale: true,
                labels: {
                    style: {
                        colors: ['#8392a5'],
                        fontSize: '10px',
                    },
                    formatter: function (value: any) {
                        return (value / 10000).toFixed(0) + '만원';
                    },
                },
            },
            legend: {
                show: true, // legend를 표시합니다.
                position: 'top', // legend의 위치를 지정할 수 있습니다.
                horizontalAlign: 'right', // legend의 수평 정렬을 지정할 수 있습니다.
                onItemClick: {
                    toggleDataSeries: true, // 데이터 시리즈를 토글할 수 있도록 설정합니다.
                },
            },
        },

        series: series,
    };
    return returnData;
};

